import { Box, Card } from "@mui/material";
import React from "react";

const AboutPage = () => {
    
    return (
        <main>
            <h1 className="big-title">О проекте</h1>
            <Box
                sx={{my: 5}} 
                className="block-addBot"
            >
                <Card className="card-purpleorange card-about">
                    <h1>Автор</h1>
                    <h3>
                        <a href="https://github.com/roso1nik" className="nopad" target='_blank' rel="noreferrer">
                            github@roso1nik
                        </a>
                    </h3>
                </Card>
                <Card className="card-blue card-about">
                    <h1>Идея</h1>
                    <h3>Для создания сайта была использована идея как можно большего упрощения UI для приятного использования<br/></h3>
                </Card>
                <Card className="card-full card-about">
                    <h1>Cтек технологий</h1>
                    <h3>JavaScript · TypeScript · NodeJS · React · Material-UI</h3>
                </Card>
            </Box>
        </main>
    )
}

export default AboutPage;