import React, { useState } from 'react';
import { BrowserRouter } from 'react-router-dom'
import AppRouter from './components/AppRouter';
import './App.css';
import chalk from 'chalk'
import NavBar from './components/NavBar';
import { Container } from '@mui/material';
import { balanceContext } from "./context/index"

const App = () => {
  console.log(chalk.whiteBright.bgCyan('о, а Вы, похоже, программист, раз сюда попали'))

  const [howMuchBuy, setHowMuchBuy] = useState(0)
  const [buySummary, setBuySummary] = useState(0)
  const [basket, setBasket] = useState([]);

  // баланс:
  const [balance, setBalance] = useState(1500) // <--сюда вписываем баланс
  
  // бд книг:
  const [books, setBooks] = useState([
    {
        id: 1,
        name: 'Совершенный код. Мастер-класс',
        price: 100,
        amount: 3
    },
    {
        id: 2,
        name: 'Rapid Development, Steve McConnell',
        price: 180,
        amount: 2
    },
    {
        id: 3,
        name: 'Искусство программирования, Д.Кнут',
        price: 210,
        amount: 1
    },
  ]);

  return (
    <balanceContext.Provider value={{
      balance,
      setBalance,
      books,
      setBooks,
      howMuchBuy,
      setHowMuchBuy,
      buySummary,
      setBuySummary,
      basket,
      setBasket
    }}>
      <BrowserRouter>
        <NavBar />
        <Container maxWidth="xl">
          <AppRouter />
        </Container>
      </BrowserRouter>
    </balanceContext.Provider>
  );
}

export default App;