import { Box, Button, Card, CardContent, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Divider, Slide } from "@mui/material";
import React, { useContext } from "react";
import { balanceContext } from "../context/index"
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const BasketPage = () => {

    const {balance, howMuchBuy, buySummary, basket} = useContext(balanceContext)
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <main>
            <h1 className="big-title">Личный Кабинет</h1>
            <Box
                sx={{my: 5}} 
                className="block-addBot bpbab"
            >
                <Card>
                    <CardContent className="gap-1">
                        <h1>Баланс</h1>
                        <div className="flex-row jsb">
                            <h2>На счету:</h2>
                            <p>{balance}&nbsp;₽</p>
                        </div>
                        <div className="flex-row jsb">
                            <h2>Скидка покупателя:</h2>
                            <p>0%</p>
                        </div>
                        <Button 
                            className="wfull gotomoney"
                            onClick={handleClickOpen}
                        >
                            Пополнить
                        </Button>
                    </CardContent>
                </Card>
                <Card>
                    <CardContent className="gap-1">
                        <h1>Корзина</h1>
                        <div>
                            {basket.length === 0
                                ? <p className="note">В корзине ничего нет</p>
                                : <div className="wff">
                                    {Object.keys(basket).map((item) => (
                                        <div className="flex-row jsb wff">
                                            <p>{basket[item][0]}</p>
                                            <p>{basket[item][1]}&nbsp;₽</p>
                                        </div> 
                                    ))}
                                  </div>
                            }
                        </div>
                        <Divider />
                        <div className="flex-row jsb">
                            <h2>Товаров:</h2>
                            <p>{howMuchBuy}</p>
                        </div>
                        <div className="flex-row jsb">
                            <h2>Итого:</h2>
                            <p>{buySummary}&nbsp;₽</p>
                        </div>
                        <Button 
                            className="wfull gotobuy"
                            onClick={handleClickOpen}
                            endIcon={<ArrowForwardIosIcon />} 
                        >
                            Перейти к оформлению
                        </Button>
                    </CardContent>
                </Card>
            </Box>
            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogTitle>{"Так не получится"}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-slide-description">
                        {basket.length === 0
                            ? <p>Для оформления заказа нужен хотя бы 1 товар в корзине</p>
                            : <p>А вообще было бы неплохо получить книги на виртуальные деньги в реальности... Эх</p>
                        }
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Окей</Button>
                </DialogActions>
            </Dialog>
        </main>
    )
}

export default BasketPage;