import { Box } from "@mui/material";
import React, { useContext } from "react";
import ShopItem from "./ShopItem";
import { balanceContext } from "../context/index";

const ShopPage = () => {
    const {balance, books} = useContext(balanceContext)
    
    return (
        <main>
            <h1 className="big-title">Доступные книги</h1>
            <p>Баланс: {balance}</p>
            <Box 
                sx={{my: 5}} 
                className="block-addBot"
            >
                {Object.keys(books).map((item) => (
                    <ShopItem 
                        title={books[item].name} 
                        price={books[item].price}
                        id={books[item].id}
                        amount={books[item].amount}
                        key={books[item].id}
                    />
                ))}
            </Box>
            <p className="note">Вы долистали до конца</p>
        </main>
    )
}

export default ShopPage;