import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import { useNavigate } from 'react-router-dom';
import { STARTPAGE_ROUTE, BASKET_ROUTE, ABOUT_ROUTE } from '../routes/consts';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import MenuBookIcon from '@mui/icons-material/MenuBook';


const NavBar = () => {
  const [anchorElNav, setAnchorElNav] = React.useState(null);

  const history = useNavigate()

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  return (
    <AppBar position="static">
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Typography
            variant="h6"
            noWrap
            component="div"
            className="logo"
            sx={{ mr: 2, display: { xs: 'none', md: 'flex' } }}
            onClick={() => history(STARTPAGE_ROUTE)}
          >
            <MenuBookIcon />
            Магазин Книг
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
            <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
                <MenuItem onClick={handleCloseNavMenu}>
                    <Typography textAlign="center">
                        <Button 
                            className='menu-mob-links' 
                            onClick={() => history(ABOUT_ROUTE)}
                        >
                            О проекте
                        </Button>
                    </Typography>
                </MenuItem>
            </Menu>
          </Box>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}
            onClick={() => history(STARTPAGE_ROUTE)}
          >
            Магазин Книг
          </Typography>
          <Box 
            sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}
            className="header-buttons"
          >
            <Button
                variant='text'
                sx={{ my: 2, color: 'white', display: 'block' }}
                onClick={() => history(ABOUT_ROUTE)}
            >
                О проекте
            </Button>
          </Box>
          <Typography textAlign="center" className='header-right'>
            <Button 
                variant='text'
                className='add-button gap-2'
                sx={{ my: 2, color: 'white', display: 'block' }}
                onClick={() => history(BASKET_ROUTE)}
            >
              Личный кабинет
              <AccountCircleIcon />
            </Button>
          </Typography>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
export default NavBar;