import { Button, Card, CardActions, CardContent, Typography } from "@mui/material";
import { observer } from "mobx-react-lite";
import React, { useContext } from "react";
import { balanceContext } from "../context/index"

const ShopItem = observer(({title, price, id, amount}) => {
    const {balance, setBalance, books, howMuchBuy, setHowMuchBuy, buySummary, setBuySummary, basket, setBasket} = useContext(balanceContext)

    const buyBook = (id) => {
        if(price <= balance) {
            setBalance(balance - price)
            setHowMuchBuy(howMuchBuy + 1)
            setBuySummary(buySummary + price)
            books[id - 1].amount -= 1;
            setBasket([...basket, [title, price]])
            console.log(basket)
        }
    }

    return (
        <Card 
            sx={{ maxWidth: 345 }} 
            className="itemShop"
            title={title}
        >
            <CardContent className="itemShop-up">
                <p className="note">Книга</p>
                <Typography gutterBottom variant="h5" component="div">
                    {title}
                </Typography>
            </CardContent>
            <CardActions className="itemShop-price">
                <Typography variant="h5" component="div" className="price-block nosell">
                    <p>
                        {price}&nbsp;₽
                    </p>
                </Typography>
                <div className="flex-row cart-options">
                    {amount > 0
                        ?<Button 
                            variant="contained" 
                            size="large"
                            className="wfull itemShop-button"
                            onClick={() => {buyBook(id)}}
                            title={`Купить ${title}`}
                         >
                            Купить
                         </Button>
                        : <Button 
                            variant="contained" 
                            size="large"
                            className="wfull itemShop-button button-disabled"
                            onClick={() => {buyBook(id)}}
                            disabled
                         >
                            Товара нет
                         </Button>
                    }
                </div>
            </CardActions>
            <CardActions>
                <Typography variant="body2" color="text.secondary" className="note">
                    Осталось книг: {amount}
                </Typography>
            </CardActions>
        </Card>
    )
})

export default ShopItem;